export default {
  namespaced: true,
  state: {
    // 
    value: '' // medium small mini
  },
  actions: {
    /**
     * @description 
     * @param {Object} state vuex state
     * @param {String} size 
     */
    set ({ state, dispatch }, size) {
      return new Promise(async resolve => {
        // store 
        state.value = size
        // 
        await dispatch('d2admin/db/set', {
          dbName: 'sys',
          path: 'size.value',
          value: state.value,
          user: true
        }, { root: true })
        // end
        resolve()
      })
    },
    /**
     * @description 
     * @param {Object} state vuex state
     */
    load ({ state, dispatch }) {
      return new Promise(async resolve => {
        // store 
        state.value = await dispatch('d2admin/db/get', {
          dbName: 'sys',
          path: 'size.value',
          defaultValue: 'default',
          user: true
        }, { root: true })
        // end
        resolve()
      })
    }
  }
}
