import dayjs from 'dayjs'
import { get, toString } from 'lodash'
import util from '@/libs/util.js'

export default {
  namespaced: true,
  state: {
    // 
    list: []
  },
  getters: {
    /**
     * @description  log (all) 
     * @param {*} state vuex state
     */
    length (state) {
      return state.list.length
    },
    /**
     * @description  log (error) 
     * @param {*} state vuex state
     */
    lengthError (state) {
      return state.list.filter(l => l.type === 'error').length
    }
  },
  actions: {
    /**
     * @description 
     * @param {Object} param type {String} 
     * @param {Object} param err {Error} 
     * @param {Object} param vm {Object} vue 
     * @param {Object} param info {String} 
     */
    add ({ state, rootState }, { type, err, vm, info }) {
      // store 
      state.list.push(Object.assign({
        // 
        type: 'log', // or error
        // 
        info: '',
        // 
        err: '',
        // vue 
        vm: '',
        // 
        user: rootState.d2admin.user.info,
        //  uuid
        uuid: util.cookies.get('uuid'),
        //  token
        token: util.cookies.get('token'),
        // 
        url: get(window, 'location.href', ''),
        // 
        time: dayjs().format('YYYY-M-D HH:mm:ss')
      }, {
        type,
        err,
        vm,
        info: toString(info)
      }))
    }
  },
  mutations: {
    /**
     * @description 
     * @param {Object} state vuex state
     */
    clean (state) {
      // store 
      state.list = []
    }
  }
}
